import 'amfe-flexible';
import Vue from 'vue';
import App from './App';
import router from './router/index';
import {wxConfig,wxGetLocation} from './libs/wxConfig/index'
Vue.prototype.wxConfig = wxConfig;
Vue.prototype.wxGetLocation = wxGetLocation;
import config from '@/config';
import { Tabbar, TabbarItem ,Field ,Icon, Tab, Tabs,List,Toast,Row, Col,Notify } from 'vant';
Vue.use(Tabbar).use(TabbarItem).use(Field).use(Icon).use(Tab).use(Tabs).use(List).use(Toast);
Vue.use(Toast).use(Row).use(Col).use(Notify)
Vue.prototype.$config = config;
Vue.prototype.Toast = Toast;


new Vue({
    router,
    el: '#app',
    render: h => h(App)
});
